import React from "react";
import PropTypes from "prop-types";
import cx from 'classnames';
import "./LandingSection.scss";

const LandingSection = ({
  className,
  children,
  title,
  subTitle,
  isLight,
  contentAlignment,
  titleAlignment
}) => {
  const headerClasses = cx('LandingSection-title', {
    'LandingSection-title--alignCenter': titleAlignment === 'center',
    'LandingSection-title--alignLeft': titleAlignment === 'left',
    'LandingSection-title--textLight': isLight
  });
  const contentClasses = cx(`LandingSection-content ${className}` , {
    'LandingSection-content--alignCenter': contentAlignment === 'center',
    'LandingSection-content--alignLeft': contentAlignment === 'left',
  })

  return (
    <div className="LandingSection"> 
      {title && (
        <header className={headerClasses}>
          <h1>{title}</h1>
          {
            subTitle && (
              <h2>{subTitle}</h2>
            )
          }
      </header>
      )}
      <div className={contentClasses}>
        {children}
      </div>
    </div>
  )
};

LandingSection.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  subTitle: PropTypes.string,
  children: PropTypes.node.isRequired,
  isLight: PropTypes.bool,
  contentAlignment: PropTypes.oneOf(['left', 'center']),
  textAlignment: PropTypes.oneOf(['left', 'center'])
};

LandingSection.defaultProps = {
  className: '',
  title: null,
  subTitle: null,
  contentAlignment: 'left',
  textAlignment: 'left',
  isLight: false
};

export default LandingSection;
